import React from 'react';
import './styles.scss';

function VoucherSuggestionItem(props) {
  const { deal } = props;

  return (
    <div onClick={props.onClick} className="voucher-suggestion-container">
      <img
        src={
          deal.image_urls && deal.image_urls.length > 0 && deal.image_urls[0]
        }
        alt="promotion"
        className="img-promotion"
      />
      <div className="promotion-right">
        <div className="title-promotion">
          <img
            src={deal.image_urls && deal.merchant.image_url}
            alt="promotion"
            className="img-store"
          />
          <p className="title-store">{deal.merchant.name}</p>
        </div>
        <p className="title">{deal.name}</p>
      </div>
    </div>
  );
}

export default React.memo(VoucherSuggestionItem);
