/* eslint-disable no-unused-vars */
/* eslint-disable no-unneeded-ternary */
/* eslint-disable no-return-assign */
/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import Hidden from '@material-ui/core/Hidden';
import Slider from 'react-slick';
import classnames from 'classnames';
import ArrowLeft from '../../assets/icons/icon-arrow-left.png';
import ArrowRight from '../../assets/icons/icon-arrow-right.png';
import './SlideShow.scss';

function NextArrow(props) {
  const { style, onClick } = props;
  return (
    <div
      className="next_arrow_slide"
      style={{ ...style, right: '15px' }}
      onClick={onClick}
    >
      <img src={ArrowRight} alt="right" />
    </div>
  );
}

function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className="pre_arrow_slide"
      style={{ ...style, left: '15px', zIndex: 1 }}
      onClick={onClick}
    >
      <img src={ArrowLeft} alt="left" />
    </div>
  );
}

function NextArrowOtherSlide(props) {
  const { className, onClick } = props;
  return (
    <div
      className={classnames(className, 'nextArrowOtherSlide')}
      onClick={onClick}
    >
      <img src={ArrowRight} alt="right" />
    </div>
  );
}

function PrevArrowOtherSlide(props) {
  const { className, onClick } = props;
  return (
    <div
      className={classnames(className, 'preArrowOtherSlide')}
      onClick={onClick}
    >
      <img src={ArrowLeft} alt="left" />
    </div>
  );
}

function renderOtherSlider(slides, settings) {
  if (!slides) return null;

  const styles =
    slides.length < 5
      ? {
          width: 150 * slides.length,
        }
      : null;
  return (
    <div className="wrapperSliderWithoutArror" style={styles}>
      <Slider {...settings}>
        {slides.map((item) => (
          <div className="slideOtherItem" key={item}>
            <img src={item} alt="img" />
          </div>
        ))}
      </Slider>
    </div>
  );
}

class SlideShow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      nav1: null,
      nav2: null,
    };
  }

  componentDidMount() {
    this.setState({
      nav1: this.slider1,
      nav2: this.slider2,
    });
  }

  render() {
    const { width } = this.props;
    const { items } = this.props;
    const slides = items || [];
    const settings1 = {
      asNavFor: this.state.nav2,
      ref: (slider) => (this.slider1 = slider),
      arrows: false,
    };
    const settings2 = {
      asNavFor: this.state.nav1,
      ref: (slider) => (this.slider2 = slider),
      focusOnSelect: true,
      swipeToSlide: true,
      speed: 500,
      responsive: [
        {
          breakpoint: 2880,
          settings: {
            slidesToShow: slides.length >= 5 ? 5 : slides.length,
          },
        },
        {
          breakpoint: 1920,
          settings: {
            slidesToShow: slides.length >= 5 ? 5 : slides.length,
          },
        },
        {
          breakpoint: 1280,
          settings: {
            slidesToShow: slides.length >= 3 ? 3 : slides.length,
          },
        },
        {
          breakpoint: 960,
          settings: {
            slidesToShow: slides.length >= 2 ? 2 : slides.length,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: slides.length >= 2 ? 2 : slides.length,
          },
        },
      ],
      nextArrow: <NextArrowOtherSlide />,
      prevArrow: <PrevArrowOtherSlide />,
    };
    const settings3 = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };
    return (
      <div
        className={classnames(
          'slide_show_container',
          this.props.customStyleContainer
        )}
      >
        <Hidden xsDown>
          <div className="main_slide">
            {slides ? (
              <Slider {...settings1}>
                {slides.map((item) => (
                  <div key={item}>
                    <img
                      className={classnames(
                        'big_image_show',
                        this.props.customImageStyle
                      )}
                      src={item}
                      alt="img"
                    />
                  </div>
                ))}
              </Slider>
            ) : null}
          </div>
          <div className="other_slide">
            {renderOtherSlider(slides, settings2)}
          </div>
        </Hidden>
        <Hidden smUp>
          <div className="main_slide">
            {slides ? (
              <Slider {...settings3}>
                {slides.map((item) => (
                  <div key={item}>
                    <img
                      className={classnames(
                        'big_image_show',
                        this.props.customImageStyle
                      )}
                      src={item}
                      alt="img"
                    />
                  </div>
                ))}
              </Slider>
            ) : null}
          </div>
        </Hidden>
      </div>
    );
  }
}

export default SlideShow;
