import React from 'react';
import './AddressApply.scss';
import { DirectionIcon } from 'src/assets/icons';
import { formatDistance } from 'src/utils/formatString';

const AddressApply = (props) => {
  const { address, distance } = props;
  return (
    <div className="address-apply-container">
      <div className="address-distance">
        <p className="address">{address}</p>
        <p className="distance">{formatDistance(distance)}</p>
      </div>
      <div
        onClick={() => {
          window.open(
            `https://maps.google.com/?q=${props.location.lat},${props.location.lng}`
          );
        }}
      >
        <DirectionIcon style={{ width: '30px', height: '30px' }} />
      </div>
    </div>
  );
};

export default React.memo(AddressApply);
