/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-props-no-spreading */
import React, { useRef } from 'react';
import {
  Grid,
  withStyles,
  Box,
  Tab,
  Tabs,
  makeStyles,
} from '@material-ui/core';
import ReactMarkdown from 'react-markdown/with-html';

import {
  CartIcon,
  FBCircleIcon,
  GGCircleIcon,
  TWCircleIcon,
} from 'src/assets/icons';
import { useHistory } from 'react-router-dom';

import { Breadcrumb } from 'src/components/base';
import './happyBucketDetail.scss';
import VoucherSuggestionItem from 'src/components/base/layout/VoucherSuggestionItem';

import SlideShow from '../../../../components/SlideShow/SlideShow';
import Payment from '../../../../components/Payment';
// import Map from '../../../../components/Map/Map';

const a11yProps = (index) => {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
  },
  rootTab: {
    fontFamily: 'Google Sans',
    fontSize: '15px',
    fontWeight: '600',
    color: '#000',
    backgroundColor: '#fff',
  },
}));

const StyledTabs = withStyles({
  indicator: {
    display: 'flex',
    justifyContent: 'center',
    height: '4px',
    backgroundColor: '#fff',
    '& > span': {
      maxWidth: '100px',
      width: '100%',
      height: '5px',
      backgroundColor: '#EABFB9',
    },
  },
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />);

const VoucherDetailView = (props) => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const tabContent1 = useRef();
  const tabContent2 = useRef();
  const tabContent3 = useRef();
  const history = useHistory();
  const handleChangeTab = (event, tabIndex) => {
    if (tabIndex === 0) {
      window.scrollTo(0, tabContent1.current.offsetTop);
    } else if (tabIndex === 1) {
      window.scrollTo(0, tabContent2.current.offsetTop);
    } else {
      window.scrollTo(0, tabContent3.current.offsetTop);
    }
  };

  const renderContent = (content) => {
    return <ReactMarkdown source={content} escapeHtml={false} />;
  };
  const voucher = props.voucher || {};

  return (
    <Grid
      container
      style={{ backgroundColor: '#F6F9FF' }}
      className="voucher-detail-wrapper"
    >
      <Breadcrumb
        items={[
          { name: 'Happy Bucket', url: '/happy-bucket' },
          { name: voucher.name, url: `/happy-bucket/${voucher.id}` },
        ]}
      />

      <Grid container lg={12} className="voucher-detail-container" spacing={2}>
        <Grid
          lg={8}
          xl={8}
          md={8}
          sm={12}
          xs={12}
          className="container-left"
          item
        >
          <div
            style={{
              flexDirection: 'row',
              display: 'flex',
              alignItems: 'center',
              marginBottom: '20px',
            }}
          >
            <img
              src={voucher?.merchant?.image_url}
              alt="logo"
              className="img-logo-home"
            />
            <p className="voucher_name">{voucher.name}</p>
          </div>
        </Grid>
        <Grid lg={4} xl={4} md={4} sm={12} xs={12} item>
          <div />
        </Grid>
      </Grid>
      <Grid container className="voucher-detail-container" spacing={3}>
        <Grid
          lg={8}
          xl={8}
          md={7}
          sm={12}
          xs={12}
          className="container-left"
          item
        >
          {voucher.imageUrls && voucher.imageUrls.length > 0 && (
            <SlideShow items={voucher.imageUrls} />
          )}
          <Grid
            style={{ marginTop: '28px', marginBottom: '38px' }}
            container
            direction="row"
            alignItems="center"
          >
            <p className="share_title">Chia sẻ</p>
            <div style={{ marginLeft: '18px' }}>
              <FBCircleIcon />
            </div>
            <div style={{ marginLeft: '18px' }}>
              <GGCircleIcon />
            </div>
            <div style={{ marginLeft: '18px' }}>
              <TWCircleIcon />
            </div>
          </Grid>
          <div className={classes.root}>
            <StyledTabs
              value={value}
              onChange={handleChangeTab}
              aria-label="simple tabs example"
              variant="fullWidth"
            >
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Điều kiện áp dụng"
                {...a11yProps(0)}
              />
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Thông tin chi tiết"
                {...a11yProps(1)}
              />
              <Tab
                style={{ textTransform: 'none' }}
                classes={{
                  selected: classes.tab,
                  root: classes.rootTab,
                }}
                label="Bản đồ địa chỉ áp dụng"
                {...a11yProps(2)}
              />
            </StyledTabs>
            <Box
              display="flex"
              style={{ backgroundColor: 'transparent', marginTop: '32px' }}
            >
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent1}>
                Điều kiện áp dụng
              </p>
            </Box>
            <div className="tab_content">
              {renderContent(props.voucher.condition)}
            </div>
            <Box display="flex" style={{ backgroundColor: 'transparent' }}>
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent2}>
                Thông tin chi tiết
              </p>
            </Box>
            <div className="tab_content">
              {renderContent(props.voucher.description)}
            </div>

            <Box display="flex" style={{ backgroundColor: 'transparent' }}>
              <div
                style={{
                  width: '4px',
                  backgroundColor: '#EABFB9',
                  marginTop: '5px',
                  marginBottom: '5px',
                  marginRight: '20px',
                }}
              />
              <p className="map_title" ref={tabContent3}>
                Bản đồ địa chỉ áp dụng
              </p>
            </Box>

            {/* <Map
              // eslint-disable-next-line react/prop-types
              // google={this.props.google}
              center={{ lat: branch.lat, lng: branch.lng }}
              // height={420}
              // zoom={15}
              // isAutoComplete={false}
              apiKey={'AIzaSyAEdKiEuvazphWXEMKBT4BIgsAFPaE3AgQ'}
            /> */}
          </div>
        </Grid>

        <Grid lg={4} xl={4} md={5} sm={12} xs={12} item>
          <div className="payment">
            <Payment
              showPrice={false}
              showPaymentButton={false}
              icon={<CartIcon className="cart_icon" />}
              branches={voucher.branches}
              onClick={props.onAddItemToCartClick}
            />
          </div>
          <div className="merchant_info">
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <img
                className="merchant_logo"
                src={voucher?.merchant?.image_url}
                alt="logo"
              />
              <p className="merchant_name">{voucher?.merchant?.name}</p>
            </div>

            <p className="merchant_description">
              {voucher?.merchant?.description}
            </p>
          </div>

          <div className="promotion-section">
            <p className="promotion-title">Món ăn đề xuất</p>
            {props.suggestVoucher?.map((voucherDetail) => (
              <VoucherSuggestionItem
                onClick={() => {
                  history.push(`/voucher/${voucherDetail.id}`);
                }}
                deal={voucherDetail}
              />
            ))}
          </div>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default React.memo(VoucherDetailView);
