import React, { useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { bindActionCreators } from 'redux';
import {
  getListVoucherByMerchantHandle,
  getVoucherByIdHandle,
} from 'src/redux/actions/voucher.action';
import VoucherDetailView from './happyBucketDetail.view';

const VoucherDetailContainer = (props) => {
  const [voucher, setVoucher] = useState({});
  const [suggestVoucher, setSuggestVoucher] = useState([]);

  const dispatch = useDispatch();
  useEffect(() => {
    if (props?.match?.params?.id) {
      props.getVoucherByIdHandle(props?.match?.params?.id, (voucherDetail) => {
        setVoucher(voucherDetail);
      });
    }
  }, []);
  useEffect(() => {
    if (voucher?.merchant?.id) {
      dispatch(
        getListVoucherByMerchantHandle(
          1,
          5,
          {
            merchant_id: voucher?.merchant?.id,
          },
          (listSuggestVoucher) => {
            setSuggestVoucher(listSuggestVoucher);
          }
        )
      );
    }
  }, [voucher]);

  return (
    <VoucherDetailView suggestVoucher={suggestVoucher} voucher={voucher} />
  );
};

const mapStateToProps = () => ({});
const mapDispatchToProps = (dispatch) =>
  bindActionCreators({ getVoucherByIdHandle }, dispatch);
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(VoucherDetailContainer);
