/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable react/jsx-closing-tag-location */
import React, { useState } from 'react';
import './Payment.scss';
import { ButtonCustom, CounterInput } from 'src/components/base';
import { Rating } from '@material-ui/lab';
import { convertCurrency } from 'src/utils/currency';
import { useSelector } from 'react-redux';
import { calculateDistance } from 'src/utils/distance';
import { Hidden } from '@material-ui/core';

import { getCurrentLocationSelector } from 'src/redux/selectors/common.selector';
import AddressApply from '../AddressApply';
import BranchModal from '../BranchModal';

const Payment = (props) => {
  const [listSelectedBranch, setListSelectedBranch] = useState([]);
  const [openBranchModal, setOpenBranchModal] = useState(false);
  const currentLocation = useSelector(getCurrentLocationSelector);

  const onCloseClick = () => {
    setOpenBranchModal(false);
  };

  const { showPrice = true, showPaymentButton = true, voucher } = props;
  return (
    <div className="payment-method-container">
      <div style={{ flex: 1 }}>
        <div className="rate-section">
          <Rating name="hover-feedback" value={5} readOnly precision={0.5} />
          <p className="rate-txt">Đánh giá</p>
        </div>

        {props.branches?.length > 0 && (
          <>
            <div className="store-apply">
              <p className="title">Địa chỉ áp dụng</p>
              <AddressApply
                location={props.branches[0]}
                address={props.branches[0].address}
                distance={calculateDistance(
                  {
                    lat: currentLocation.latitude,
                    lng: currentLocation.longitude,
                  },
                  props.branches[0]
                )}
              />
            </div>

            {props.branches?.length > 1 && (
              <div className="store-other">
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                  }}
                >
                  <p className="title">{`Cửa hàng khác (${
                    props.branches.slice(1).length
                  })`}</p>
                  <p
                    onClick={() => {
                      setListSelectedBranch(props.branches);
                      setOpenBranchModal(true);
                    }}
                    className="see-all-branch"
                  >
                    Xem tất cả
                  </p>
                </div>
                {props.branches.slice(1, 3).map((branch) => (
                  <AddressApply
                    location={branch}
                    address={branch.address}
                    distance={calculateDistance(
                      {
                        lat: currentLocation.latitude,
                        lng: currentLocation.longitude,
                      },
                      branch
                    )}
                  />
                ))}
              </div>
            )}
          </>
        )}
      </div>
      <Hidden xsDown>
        {showPrice && (
          <>
            <div className="price-section">
              <div className="price">
                <p className="price-original">
                  {convertCurrency(voucher.price)}
                </p>
                <p className="price-sale-off">
                  {convertCurrency(voucher.regular_price)}
                </p>
              </div>
              <CounterInput
                value={props.quantity}
                handleDecrement={() => {
                  if (props.quantity > 1) {
                    props.onQuantityChange(props.quantity - 1);
                  }
                }}
                handleIncrement={() => {
                  props.onQuantityChange(props.quantity + 1);
                }}
                minValue={1}
              />
            </div>
          </>
        )}
        {showPaymentButton && (
          <ButtonCustom
            onClick={props.onClick}
            title={props.title || 'Mua mã ngay'}
            icon={props.icon}
            customStyle="custom-button"
          />
        )}
      </Hidden>
      <BranchModal
        listBranch={listSelectedBranch}
        open={openBranchModal}
        onCloseClick={onCloseClick}
      />
    </div>
  );
};
export default React.memo(Payment);
